import React from 'react';
import Router from 'next/router';

const NavigatorWrapper = props => {
    return (
        <div className={"w-100 p-2 pl-5 pr-5 border-bottom bg-white border-primary shadow d-flex justify-content-between"}>
            {props.children}
        </div>
    )
};

const NavigatorContent = props => {
    return (
        <div>{props.children}</div>
    );
};

const Brand = props => {
    return (
        <div><img src={"/static/images/dosenjaga.png"} height={"32px"}/></div>
    );
};

export default class DashboardStudent extends React.Component {
    render() {
        return (
            <div>
                <NavigatorWrapper>
                    <NavigatorContent>
                        <Brand/>
                    </NavigatorContent>
                </NavigatorWrapper>
            </div>
        );
    }
}


